.landing {
  padding: 100px 0;
  background-color: white;
}
.landing .container {
  height: calc(100vh - 60px);
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: black;
}
.landing .text h1 {
  font-size: 45px;
}
.landing .text h1 span {
  color: hsl(353, 93%, 46%);
}
.landing .text p {
  width: 550px;
  max-width: 100%;
  color: #444;
  margin-top: 15px;
  font-size: 11px;
  line-height: 1.8;
}
@media (max-width: 991px) {
  .landing .text p {
    width: 100%;
  }
}
.landing .image {
  width: 300px;
  height: 600px;
}
@media (max-width: 991px) {
  .landing .image {
    display: none;
  }
}
.landing .image img {
  width: 300px;
  height: 600px;
}
