.skills {
  padding: 100px 0;
  background-color: white;
}
.skills .container h2 {
  color: black;
  margin-bottom: 35px;
  font-weight: 800;
  font-size: 25px;
  font-family: "Carter One", system-ui;
}
.skills .container .card {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 50px;
}
.skills .container .card .box {
  opacity: 0;
  transform: translateY(20px);
  transition: 0.5s;
  padding: 25px;
  background-color: #262626;
  position: relative;
}
.skills .container .card .box.visible {
  opacity: 1;
  transform: translateY(0);
}
.skills .container .card .box i {
  display: block;
  margin-bottom: 25px;
  font-size: 60px;
  text-align: center;
}
.skills .container .card .box h2 {
  color: white;
  margin-bottom: 0;
  font-size: 28px;
  letter-spacing: 1px;
  text-align: center;
}
