.contact {
  padding: 100px 0;
}
.contact .container h2 {
  color: black;
  margin-bottom: 35px;
  font-weight: 800;
  font-size: 25px;
  font-family: "Carter One", system-ui;
}
.contact .container .info {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}
@media (max-width: 991px) {
  .contact .container .info {
    flex-direction: column;
  }
}
.contact .container .info,
.contact .container form {
  flex-basis: 45%;
}
@media (max-width: 991px) {
  .contact .container .info,
  .contact .container form {
    width: 100%;
    text-align: center;
  }
}
.contact .container .info .text {
  display: flex;
  flex-direction: column;
}
@media (max-width: 991px) {
  .contact .container .info .text {
    width: 100%;
    align-items: center;
    justify-content: center;
    margin: 25px auto 0;
  }
}
.contact .container .info .links {
  margin-top: 25px;
  display: flex;
  flex-direction: column;
}
.contact .container .info .links a {
  display: flex;
  align-items: center;
  margin-bottom: 25px;
}
.contact .container .info .links a i {
  margin-right: 15px;
  color: hsl(353, 93%, 46%);
}
.contact .container .info .links a span {
  color: black;
  font-size: 10px;
}
.contact .container .info .icons {
  margin-bottom: 35px;
  display: flex;
  align-items: center;
}
.contact .container .info .icons a {
  padding: 0 15px 0 0;
  color: black;
}
.contact .container .info a.cv {
  display: inline-block;
  width: fit-content;
  padding: 10px 20px;
  color: hsl(24, 86%, 90%);
  background-color: hsl(353, 93%, 46%);
  font-size: 10px;
}
.contact .container form {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
@media (max-width: 991px) {
  .contact .container form {
    order: -1;
  }
}
.contact .container form h3 {
  font-size: 26px;
}
.contact .container form input,
.contact .container form textarea {
  width: 80%;
  height: 50px;
  padding: 15px;
  margin-top: 25px;
  background-color: #f9f9f9;
  font-size: 10px;
  border: none;
  outline: none;
  border-bottom: 1px solid #c2b1b1;
}
.contact .container form textarea {
  height: 200px;
}
.contact .container form input::placeholder,
.contact .container form textarea::placeholder {
  transition: 0.3s;
}
.contact .container form input:focus::placeholder,
.contact .container form textarea:focus::placeholder {
  opacity: 0;
}
.contact .container form input[type="submit"] {
  color: white;
  background-color: hsl(353, 93%, 46%);
  font-size: 13px;
  font-weight: bold;
  font-family: "Montserrat", sans-serif;
  letter-spacing: 1px;
  cursor: pointer;
}
