.about {
  background-color: white;
}
.about .container {
  display: flex;
  align-items: center;
  color: black;
}
.about .container .image,
.about .container .text {
  flex: 1;
}
@media (max-width: 991px) {
  .about .container .image {
    display: none;
  }
  .about .container .text {
    width: 100%;
  }
}
.about .container .text h2 {
  font-size: 30px;
  margin-bottom: 20px;
  font-family: "Carter One", system-ui;
}
.about .container .text p {
  color: #444;
  font-size: 12px;
  line-height: 1.8;
}
