.services {
  padding: 100px 0;
  background-color: white;
}
.services.visible {
  
  opacity: 1;
}
.services .container h2 {
  color: black;
  margin-bottom: 35px;
  font-weight: 800;
  font-size: 25px;
  font-family: "Carter One", system-ui;
}
.services .container .cards {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 50px;
}
.services .container .cards .box {
  opacity: 0;
  transform: translateY(20px);
  transition: 0.5s;
  padding: 40px 15px;
  background-color: #262626;
}
.services .container .cards .box.visible {
  opacity: 1;
  transform: translateY(0);
}
.services .container .cards .box i {
  color: white;
  margin-bottom: 20px;
}
.services .container .cards .box .text h2 {
  color: white;
  margin-bottom: 15px;
  font-size: 18px;
  letter-spacing: 1px;
}
.services .container .cards .box p {
  color: rgba(255, 255, 255, 0.663);
  font-size: 10px;
  line-height: 1.8;
}
