header {
  height: 60px;
  background-color: #000;
}
header .container {
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
header .logo {
  color: hsl(353, 93%, 46%);
  font-size: 24px;
  font-weight: bold;
}
header nav {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
@media (max-width: 767px) {
  header .container .nav {
    display: none;
    background-color: black;
    position: absolute;
    right: 0;
    top: 100%;
    width: 50%;
    height: 600px;
    align-items: stretch;
    justify-content: flex-start;
    flex-direction: column;
  }
}
header ul {
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 767px) {
  header ul {
    flex-direction: column;
    align-items: stretch;
    justify-content: flex-start;
  }
}
header ul li {
  padding: 0 25px;
}
@media (max-width: 767px) {
  header ul li {
    padding: 25px 15px;
  }
}
header ul li a {
  color: #fff;
  font-size: 11px;
  text-decoration: none;
  font-family: "Carter One", system-ui;
}
@media (max-width: 767px) {
  header .icons {
    color: white;
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
}
header .container .icons i.times,
header .container .icons i.bars {
  display: none;
  font-size: 16px;
}
@media (max-width: 767px) {
  header .container .icons i.bars {
    display: flex;
  }
}
